body {
  font-family: sans-serif;
}

input {
  padding: 10px 20px;
  font-size: 30px;
}

.search-form {
  background-color: #00f;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.search-container {
  justify-content: center;
  display: flex;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.info {
  justify-content: space-around;
  display: flex;
}

.photo-card {
  width: 30%;
  text-align: center;
  background-color: #f5f5f5;
  border-radius: 15px;
}

img {
  width: 100%;
  height: 300px;
  transition: transform .4s ease-out;
}

img:hover {
  transform: scale(1.05);
}

.load-more {
  color: #fff;
  background-color: #00f;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  display: flex;
}

button.hidden {
  display: none;
}

